.single-post__title {
  text-align: center;
  max-width: 700px;
  margin: 20px auto 40px;
  font-size: 2.3rem;
  font-weight: 400;
  font-style: italic;
}

.single-post__img {
  position: relative;
  width: 100%;
  max-width: 1000px;
  height: 40vmax;
  overflow: hidden;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 40px;
}
.single-post__img img {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.single-post__content {
  font-size: 1.2rem;
  line-height: 1.2;
  margin-bottom: 40px;
}